
.wraper{
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}
.wraper button{
    padding: 1rem 1rem;
    background-color: #614101;
    color: #fff;
    font-weight: bold;
    font-size: .9rem;
    cursor: pointer;
}
.accordion{
    width: 38rem;
    
}
.item{
  
    background: #614101;
    margin-bottom: 1rem;
    padding: 1rem 2rem;
}
.title{
    color: #fff;
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
    cursor: pointer;
    padding: .2rem 1rem;
    /* background-color: red; */

}
.content{
    text-align: center;
    color: #ffffff;
    height: auto;

}